var React = require('react');
var ReactDOM = require('react-dom');
var ReactRouter = require('react-router');

var Router = ReactRouter.Router;
var Route = ReactRouter.Route;
var Navigation = ReactRouter.Navigation;
var History = ReactRouter.History;
var createBrowserHistory = require('history/lib/createBrowserHistory');

var Rebase = require('re-base');
var base = Rebase.createClass('https://watchitnow.firebaseio.com/');
import 'whatwg-fetch';

// <App />
var App = React.createClass({
  getInitialState : function() {
    return {
     movies : {},
    }
  },
  componentDidMount : function() {
    base.syncState('/', {
      context : this,
      state : 'movies',
    });
  },
  addMovie : function(movie) {
    var number = Math.floor((Math.random() * 1000) + 1);
    this.state.movies['movie' + number] = movie;
    this.setState({ movies : this.state.movies });
  },
  deleteMovie : function(key, status) {
    this.state.movies[key] = null;
    this.setState({ movies : this.state.movies });
  },
  addRating : function(key, rate) {
    this.state.movies[key].rating = rate;
    this.setState({movies : this.state.movies});
  },
  changeStatus : function(key) {
    this.state.movies[key].status = "watched";
    this.setState({movies : this.state.movies});
  },
  showOnlyThis : function(key) {
    // Object.keys(this.state.movies).forEach(movie => {
    //   if (!(this.state.movies[key] === this.state.movies[movie])) {
    //     this.state.movies[movie].hidden = true;
    //   }
    //   else {
    //     this.state.movies[movie].hidden = false;
    //   }
    // });
    // this.setState({movies : this.state.movies});
  },
  renderWatchedMovie : function(key) {
    if (this.state.movies[key].status == "watched") {
      return (
        <Movie avgRating={this.findAverageRating} showOnlyThis={this.showOnlyThis} key={key} deleteMovie={this.deleteMovie} index={key} findAverageRating={this.findAverageRating} details={this.state.movies[key]} addRating={this.addRating} changeStatus={this.changeStatus} />
      )
    }
  },
  renderUnwatchedMovie : function(key) {
    if (this.state.movies[key].status == "not watched") {
      return (
        <Movie avgRating={this.findAverageRating} showOnlyThis={this.showOnlyThis} key={key} deleteMovie={this.deleteMovie} index={key} findAverageRating={this.findAverageRating} details={this.state.movies[key]} addRating={this.addRating} changeStatus={this.changeStatus} />
      )
    }
  },
  findAverageRating: function(key) {
    function checkStatus(response) {
      if (response.status >= 200 && response.status < 300) {
        return response
      } else {
        var error = new Error(response.statusText)
        error.response = response
        throw error
      }
    }

    function parseJSON(response) {
      return response.json()
    }

    fetch('http://api.themoviedb.org/3/search/movie?query=' + this.state.movies[key].title + '&api_key=2bdcd2084df3a652bb353e387f140f3d')
      .then(checkStatus)
      .then(parseJSON)
      .then(function(data) {
        this.setAverageRating(data)
      }.bind(this))
      .catch(function(error) {
        console.log('request failed', error)
      })
  },
  setAverageRating: function(data) {
    this.state.movies[key].avgRating = data.results[0].vote_average;
  },
  removeAll : function() {
    this.state.movies = null;
    this.setState({movies : this.state.movies});
  },
  render : function() {
    return (
      <div class="container">
        <Header />
        <MovieList addMovie={this.addMovie} removeAll={this.removeAll} />
        <div className="movielist__container">
          <h2 className="movielist__title">Movies to watch</h2>
          <ul className="movielist unwatched">
            {Object.keys(this.state.movies).map(this.renderUnwatchedMovie)}
          </ul>
        </div>
        <div className="movielist__container">
          <h2 className="movielist__title">Watched Movies</h2>
          <ul className="movielist watched">
            {Object.keys(this.state.movies).map(this.renderWatchedMovie)}
          </ul>
        </div>
      </div>
    )
  }
});

// <Header />
var Header = React.createClass({

  render : function() {
    return (
      <div className="header">Watch It</div>
    )
  }
});

// <Movie /> li
  // Title
  // Image
  // Watched status
var Movie = React.createClass({
  getRating : function(rating) {
    var stars = [];
    if (rating === '') {
      stars.push(<p>Not yet rated</p>);
    }
    else {
      for (var i = 0; i < rating; i++) {
      stars.push(<i className="fa fa-star star" aria-hidden="true"></i>);
      }
    }
    return (
      <div>{stars}</div>
    )
  },
  handleClick : function(e) {
    var hasWatched = (this.props.details.status === 'watched' ? true : false);
    if (!hasWatched) {
      this.props.changeStatus(this.props.index);
    }
    else {
      this.showRating(e.target);
    }
  },
  showRating : function(target) {
  var rate = target.parentElement.children[0];
  rate.classList.add('show');
  },
  deleteMovieButton : function() {
    var key = this.props.index;
    this.props.deleteMovie(key);
  },
  showOnlyThisClick : function() {
    var key = this.props.index;
    this.props.showOnlyThis(key);
  },
  onButtonClick : function(e) {
    var key = this.props.index;
    var parent = e.target.parentElement.parentElement;
    parent.classList.toggle('selected');
   var rateStars =  e.target.parentElement.parentElement.parentElement;
   var rating;
   for (var i = 0; i < rateStars.children.length; i++) {
     rateStars.children[i].classList.remove('selected');
     if (rateStars.children[i] === parent) {
        for (var j = 0; j < i+1; j++) {
        rateStars.children[j].classList.add('selected');
       }
       rating = (i+1);
     rateStars.parentElement.classList.remove('show');
     }
   }
    this.props.addRating(key, rating);
  },
  getAverageRating: function() {
    this.props.findAverageRating(this.props.details.title);
  },
  render : function() {
    var details = this.props.details;
    var avgRating = details.avgRating;
    var hidden = details.hidden;
    var hasWatched = (details.status === 'watched' ? true : false);
    var hasRated = (details.rating === "" ? false : true);
    var buttonText = ((hasWatched && hasRated) ? 'Movie Rated' : (hasWatched) ? 'Rate Now' : 'Watched it');
    var buttonClass = ((hasWatched && hasRated) ? 'doneRating' : (hasWatched) ? 'rateNow' : 'watchNow');
    var hiddenClass = ((hidden) ? 'movie hidden' : 'movie visible');
    return (
      <li className={hiddenClass} onClick={this.showOnlyThisClick}>
        <div className="movie-header">
          <h2>{details.title}</h2>
          <div className="stars">{this.getRating(details.rating)}</div>
        </div>
        <div className="footer">
        <div className="rateMovie">
          <ul>
            <li><a onClick={this.onButtonClick}><i className="fa fa-star empty-star" aria-hidden="true"></i></a></li>
            <li><a onClick={this.onButtonClick}><i className="fa fa-star empty-star" aria-hidden="true"></i></a></li>
            <li><a onClick={this.onButtonClick}><i className="fa fa-star empty-star" aria-hidden="true"></i></a></li>
            <li><a onClick={this.onButtonClick}><i className="fa fa-star empty-star" aria-hidden="true"></i></a></li>
            <li><a onClick={this.onButtonClick}><i className="fa fa-star empty-star" aria-hidden="true"></i></a></li>
          </ul>
        </div>
        <button onClick={this.handleClick} disabled={hasRated} className={buttonClass}>{buttonText}</button>
        </div>
        <div className="avgRating">{this.avgRating}</div>
        <button className="delete-movie" onClick={this.deleteMovieButton}>Remove this movie</button>
      </li>
    )
  }
});

var AddMovieForm = React.createClass({
  createMovie: function(event) {
    event.preventDefault();
    const radioButtons = [... event.target.querySelectorAll('input[type="radio"]')];
    var status;
    radioButtons.forEach((radio) => {
      if (radio.checked === true) {
        status = radio.value;
      }
    });
    var movie = {
      title : this.refs.title.value,
      status : status,
      rating : "",
      hidden : false,
      avgRating : ""
    }
    this.props.addMovie(movie);
    this.refs.movieForm.reset();
  },
  getMovieQuery: function() {
    var movieSuggestions = document.querySelector('.movie-suggestions');
    var query = this.refs.title.value;
    if (query === '') {
      movieSuggestions.style.display = 'none';
    }
    function checkStatus(response) {
      if (response.status >= 200 && response.status < 300) {
        return response
      } else {
        var error = new Error(response.statusText)
        error.response = response
        throw error
      }
    }

    function parseJSON(response) {
      return response.json()
    }

    fetch('http://api.themoviedb.org/3/search/movie?query=' + query + '&api_key=2bdcd2084df3a652bb353e387f140f3d')
      .then(checkStatus)
      .then(parseJSON)
      .then(function(data) {
        this.sendData(data);
      }.bind(this))
      .catch(function(error) {
        console.log('request failed', error)
      })
  },
  showMovies: function(list) {
    var movies = [... list.results];
    var movieSuggestions = document.querySelector('.movie-suggestions');
    movieSuggestions.style.display = 'flex';
    movieSuggestions.innerHTML = '';
    movies.forEach(movie => {
      var li = document.createElement('li');
      var listItem = document.createElement('a');
      listItem.onclick = function(event) {
        var date = event.target.querySelector('span');
        event.target.removeChild(date);
        var selected = event.target.innerHTML;
        var inputField = document.querySelector('.movie-form__title');
        inputField.value = selected;
        movieSuggestions.style.display = 'none';
      };
      var listItemContent = document.createTextNode(movie.original_title);
      var date = document.createElement('span');
      var dateContent = document.createTextNode(movie.release_date.split("-",1));
      date.appendChild(dateContent);
      listItem.appendChild(listItemContent);
      listItem.appendChild(date);
      li.appendChild(listItem);
      movieSuggestions.appendChild(li);
    });
  },
  sendData: function(data) {
    this.showMovies(data)
  },
  render: function() {
    return (
      <div className="movie-form">
        <form ref="movieForm" onSubmit={this.createMovie}>
          <div className="movie-form__top-content">
            <div className="inputFields">
              <input type="text" ref="title" className="movie-form__title" placeholder="Movie Title" onChange={this.getMovieQuery} />
              <ul className="movie-suggestions"></ul>
            </div>
            <div className="movie-form__status-buttons">
              <div>
                <input type="radio" id="unwatched-radio" ref="status" value="not watched" name="status" className="movie-form__status" />
                <label for="unwatched-radio" className="movie-form__status-label">
                  Want to watch
                </label>
              </div>
              <div>
                <input type="radio" id="watched-radio" ref="status" value="watched" name="status" className="movie-form__status" />
                <label for="watched-radio" className="movie-form__status-label">
                  Watched
                </label>
              </div>
            </div>
          </div>
          <button type="submit" className="movie-form__submit">Add Movie</button>
        </form>
        <button onClick={this.props.removeAll} className="movie-form__submit movie-form__submit--delete">Remove All Movies</button>
      </div>
    )
  }
});

var MovieList = React.createClass({
  render: function() {
    return (
      <AddMovieForm {...this.props}/>
    )
  }
})

var routes = (
  <Router history={createBrowserHistory()}>
    <Route path="/" component={App}/>
  </Router>
)

ReactDOM.render(<App />, document.querySelector('.app'));



